import React, { Component } from 'react';
import { Form, Input, Button, Select, message } from 'antd';
import moment from 'moment'
import { connect } from 'react-redux';
import { adminService } from '../../../actions/Company/admin.service'
import * as companyActions from '../../../actions/Company/requestCompany'
import { withRouter } from 'react-router-dom'

const { Option } = Select;


class DetailCompany extends Component {
    constructor() {
        super();
        this.state = {
            description: '',
            address: '',
            disable: true,
            loading: false
        };
    }

    onCancel = (e) => {
        e.preventDefault();
        this.props.history.push('/company/profile-admin')
    }
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll(async (err, values) => {
            if (!err) {
                await this.setState({
                    disable: true,
                    loading: true
                })
                let account = {
                    companyname: values.companyname,
                    address: values.address,
                    email: values.email,
                    phone: `${values.prefix} ${values.phone}`,
                    description: values.description,
                    avatar: this.props.userCompany.avatar ? this.props.userCompany.avatar : localStorage.getItem('res').avatar,
                    createTime: moment().unix(),
                    updateTime: moment().unix(),
                }
               
                // this.props.form.resetFields([fullname])
                message.loading('ang cp nht thng tin', 0.5)
                    .then(() => {
                        adminService.editCompany(account)
                            .then(res => {
                                if (res.status === 200) {
                                    message.success('Cp nht thng tin thnh cng!');
                                }
                                this.props.history.push('/company/profile-admin')
                                this.setState({
                                    disable: false,
                                    loading: false
                                })
                            })
                            .catch(err => {
                                
                                message.error('Li. Phin bn vui lng kim tra li')
                                this.setState({
                                    disable: false,
                                })
                            })
                    });

            }
            else {
                this.setState({
                    disable: false,
                })
            }
        });
    };
    onCheckCompanyName = (rule, value, callback) => {
        const reg = /^[a-z]|^\s|[A-z]{8}|\S{8}|[`~!@#$%^&*()(\-)_=+[(\]){};:'"<>/?\\|]/
        
        // ch ci vit thng, 
        //bt u bng khong trng, 8 k t lin nhau (tn: Nghing), k t c bit
        if ((!Number.isNaN(value) && reg.test(value)) || value === '' || value.length < 4) {
            callback('Vui lng nhp ng tn!');
        }
        else {
            callback()
        }
    }
    onCheckPhoneNumber = (rule, value, callback) => {
        const reg = /^[1-9]?([1-9][0-9]*)?$/;
        if ((!Number.isNaN(value) && reg.test(value) && value.length === 9)) {
            callback();
        }
        else {
            callback('Vui lng nhp ng s in thoi!')
        }
    };
    componentDidMount() {
        this.setState({ disable: false })
        this.props.onGettingCompanyInfo()

    }
    render() {
        const { loading } = this.state
        
        const { getFieldDecorator } = this.props.form;
        const prefixSelector = getFieldDecorator('prefix', {
            initialValue: '84',
        })(
            <Select style={{ width: 70 }}>
                <Option value="84">+84</Option>
                <Option value="86">+86</Option>
            </Select>,
        );
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 18 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 24,
                    offset: 0,
                },
            },
        };
        let { company } = this.props;
        let phoneTmp = ''
        if (company !== {}) {
            phoneTmp = `${company.phone}`
            // let str = "42 3"
            let n = phoneTmp.search(" ")
            if (n !== -1) {
                phoneTmp = phoneTmp.slice(n + 1)
            }
            else {
                phoneTmp = company.phone
            }
        }
       
        return (
            <div>
                <div className="my-address" style={{ padding: '0px 0px' }}>
                    <div className="main-title-2">
                        <h1><span>Thng tin ti khon</span></h1>
                    </div>
                    {/* <form >
                        <div className="form-group">
                            <label><b>Tn cng ty</b></label>
                            <input type="text" className="input-text" name="your name" placeholder="Tn cng ty" value={company.companyname}/>
                        </div>
                        <div className="form-group">
                            <label><b>a ch</b></label>
                            <input type="text" className="input-text" name="agent" placeholder="a ch ca cng ty" value={company.address}  />
                        </div>
                        <div className="form-group">
                            <label><b>S in thoi</b></label>
                            <input type="number" className="input-text" name="phone" placeholder="Nhp s in thoi ca cng ty bn" value={company.phone}/>
                        </div>
                        <div className="form-group">
                            <label><b>Email</b></label>
                            <input type="email" className="input-text" name="email" placeholder="Email ca cng ty" value={company.email}/>
                        </div>
                        <div className="form-group">
                            <label><b>M t</b></label>
                            <textarea className="input-text" name="message" placeholder="Nhp m t v cng ty ca bn" value={company.description}/>
                        </div>
                        <a href="true" className="btn button-md button-theme">Save Changes</a>
                    </form> */}
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="Tn cng ty" style={{ paddingRight: '20px' }} hasFeedback>
                            {getFieldDecorator('companyname', {
                                rules: [
                                    {
                                        min: 10,
                                        required: true,
                                        message: 'Vui lng nhp tn cng ty',
                                    },
                                    {
                                        validator: this.onCheckCompanyName,
                                    }
                                ],
                                initialValue: company.companyname
                            })(<Input
                                //onChange={this.onChange} 

                                style={{ marginRight: '30px' }}
                                placeholder="Nhp tn ca cng ty (Bt u bng ch in hoa)"
                                maxLength={100}
                            />)}
                        </Form.Item>
                        <Form.Item label="a ch" style={{ paddingRight: '20px' }} hasFeedback>
                            {getFieldDecorator('address', {
                                rules: [
                                    {
                                        min: 10,
                                        required: true,
                                        message: 'Vui lng nhp a ch cng ty!',
                                    },
                                    {
                                        validator: this.onCheckCompanyName,
                                    },
                                ],
                                initialValue: company.address
                            })(<Input
                                //onChange={this.onChange} 
                                style={{ marginRight: '30px' }}
                                placeholder="Nhp a ch ca cng ty (Bt u bng ch in hoa)"
                                maxLength={50} />)}
                        </Form.Item>
                        <Form.Item label="E-mail" style={{ paddingRight: '20px' }} hasFeedback>
                            {getFieldDecorator('email', {
                                rules: [
                                    {
                                        type: 'email',
                                        message: 'Vn bn khng ng nh dng email',
                                    },
                                    {
                                        required: true,
                                        message: 'Vui lng nhp email ca nhn vin vo  vn bn',
                                    },
                                ],
                                initialValue: company.email
                            })(<Input style={{ marginRight: '30px' }} />)}
                        </Form.Item>
                        <Form.Item label="S in thoi" style={{ paddingRight: '20px' }} hasFeedback>
                            {getFieldDecorator('phone', {
                                rules: [
                                    {
                                        required: true,
                                        message: 'Vui lng nhp s in thoi ca nhn vin!',
                                    },
                                    {
                                        validator: this.onCheckPhoneNumber,
                                    },
                                ],
                                initialValue: phoneTmp
                            })(<Input
                                addonBefore={prefixSelector}
                                //onChange={this.onChange} 
                                style={{ marginRight: '30px' }}
                                placeholder="Nhp 9 ch s sau s 0"
                                maxLength={9} />)}
                        </Form.Item>
                        <Form.Item label="M t" style={{ paddingRight: '20px' }}>
                            {getFieldDecorator('description', {
                                initialValue: company.description

                            })(<Input.TextArea
                                //onChange={this.onChange} 
                                style={{ marginRight: '30px' }}
                                placeholder="Nhp m t v cng ty(Bt u bng ch in hoa)"
                            />)}
                        </Form.Item>
                        <Form.Item {...tailFormItemLayout} style={{ textAlign: 'right', paddingRight: '20px' }}>
                            <Button type="primary" style={{ marginRight: '5px' }} htmlType="submit" disabled={loading}>
                                {loading && (
                                    <i
                                        className="fa fa-refresh fa-spin"
                                        style={{ marginRight: "5px" }}
                                    />
                                )}
                                {loading && <span>ang cp nht...</span>}
                                {!loading && <span>Cp nht</span>}
                            </Button>
                            <Button type="danger" onClick={this.onCancel} disabled={loading}>
                                Hy
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
            </div>
        );
    }
}

const mapDispathToProp = (dispatch) => {
    return {
        onGettingCompanyInfo: () => dispatch(companyActions.actGetInfoUserCompany())
    }
}
const mapStateToProp = (state) => {
    return {
        userCompany: state.userCompany
    }
}

export default connect(mapStateToProp, mapDispathToProp)(withRouter(Form.create()(DetailCompany)));