/* eslint-disable */
import React, { Component } from 'react';
import MainHeader from '../../components/MainHeader'
import Footer from '../../components/Footer'
import { withRouter } from 'react-router-dom'
import { service } from '../../actions/service'
import { message, PageHeader } from 'antd'
const loading = "data:image/gif;base64,";
class VerifyEmployee extends Component {
    constructor(props) {
        super(props);

        this.state = {
            idCompany: this.props.match.params.idc,
            idEmployee: this.props.match.params.ide,
            hash: this.props.match.params.hash,
            isLoading: true,
            success: false,
        };
    }
    componentDidMount() {
        if (localStorage.getItem('res')) {
            this.props.history.push('/')
        }
        else if (localStorage.getItem('company')) {
            this.props.history.push('/company/profile-admin')
        }
        const postParam = {
            company: this.state.idCompany,
            id: this.state.idEmployee,
            hash: this.state.hash,
        }
        // console.log(postParam);
        service.verifyEmployee(postParam)
            .then(res => {
                if (res.status === 200) {
                    this.setState({
                        isLoading: false,
                        success: true,
                    })
                    message.success('Xc nhn thnh cng')
                }
            })
            .catch(err => {
                // console.log(err)
                this.setState({
                    isLoading: false,
                    success: false,
                })
                message.error('Li, mi bn vui lng th li')
            })
    }
    onLogin = (e) => {
        e.preventDefault();
        this.props.history.push('/login')
    }
    render() {

        return (
            <div>
                <MainHeader />
                <div className="container">
                    <div className="verify" style={{ height: '50px', paddingTop: '10px', paddingLeft: '10px', marginTop: '70px', backgroundColor: 'lightblue' }}>
                        <h3>Xc thc ti khon nhn vin</h3>
                    </div>
                    {!this.state.isLoading && this.state.success ?
                        <div className="jumbotron">
                            <h4>Xc thc thnh cng</h4>
                            <p style={{ fontSize: '16px' }}>Ti khon  c xc thc thnh cng, hy ng nhp  tip tc s dng dch v</p>
                            <button style={{ width: '100px' }} type="button" class="btn btn-block btn-primary" onClick={this.onLogin}>ng nhp</button>
                        </div> :
                        <div>
                            {!this.state.isLoading && !this.state.success ?
                                <div className="jumbotron">
                                    <h4>Li xc thc</h4>
                                    <p style={{ fontSize: '16px' }}>Ti khon khng tn ti hoc  c xc thc, vui lng kim tra li</p>
                                </div> :
                                <div className="jumbotron">
                                    <h4>Vui lng ch trong giy lt ...</h4>
                                    <img src={loading} alt="" />
                                </div>
                            }
                        </div>
                    }
                </div>
                <Footer />
            </div>
        );
    }
}

export default withRouter(VerifyEmployee);